@import 'navigation';
@import 'testView';
@import 'threeText';
@import 'footer';
@import 'text';

$maxContendWidth: 950px;

body {
    background-color: #26292D;
    .imprint {
         .Cfooter {
            position: absolute;
            bottom: 0px; } }

    .dataProtection, .imprint {
        min-height: 100vh;
        nav {
            padding-top: 20px;
            padding-bottom: 50px; } }


    .App, .dataProtection, .imprint {
        max-width: $maxContendWidth;
        margin: 0 auto;
        color: white;
        font-size: 16px;
        padding: 0 20px;
        font-family: Helvetica Neue, Helvetica,  Roboto, sans-serif;

        main {
            iframe {
                position: relative;
                left: 50%;
                transform: translate(-50%);
                margin-top: 50px;
                max-width: 500px;
                width: 100%; }

            a {
                text-decoration: none;
                color: #FFF; }
            h4 {
                text-align: center;
                font-size: 2.3em;
                font-weight: bold;
                position: relative;
                display: inline-block;
                left: 50%;
                transform: translate(-50%,0);
                margin-bottom: 1.7em;
                padding-bottom: 10px;

                &::after {
                    content: " ";
                    position: absolute;
                    background: white;
                    width: 100px;
                    height: 10px;
                    z-index: -1;
                    left: 50%;
                    top: 100%;
                    transform: translate(-50%);
                    border-radius: 10px; } } } } }
